@import 'styles/variables';

.home {
  width: 100vw;
  height: calc(100vh - 36px);
  background-image: url('../../../images/heroBg.webp');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 36px 16px 16px;

  @media screen and (min-width: $min_tablet) {
    padding: 186px 96px;
    justify-content: flex-start;
    align-items: flex-start;
  }
  @media screen and (orientation: landscape) and (max-height: 420px) {
    padding: 40px 16px 16px;
    justify-content: center;
    align-items: center;
  }

  @media screen and (orientation: portrait) {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.title {
  color: var(--light_color);
  font-family: Jura;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  @media screen and (min-width: $min_tablet) {
    font-size: 24px;
  }
}

.titleWrap {
  display: flex;
  flex-direction: column;
  margin-bottom: 70px;
  height: 64px;

  @media screen and (orientation: landscape) and (max-heit: 420px) {
    margin-bottom: 32px;
    margin-left: auto;
    margin-right: auto;
  }
}

.logo {
  width: 120px;
  height: 136px;
  margin-bottom: 88px;

  @media screen and (orientation: landscape) {
    display: none;
  }

  @media screen and (orientation: portrait) {
    display: block;
  }
}

.btn {
  width: 200px;
}
